.section {
	padding: 50px 0;
	&_sale {
		padding: 65px 0 35px;
		background: url(../img/dice-left.png) no-repeat top 165px left 20px, url(../img/dice-right.png) no-repeat bottom 30px right 20px;
	}
	&_brands {
		padding: 20px 0;
	}
	&_gray-bg {
		background-color: #f6f6f6;
	}
	&_tabs {
		padding: 70px 0 40px;
	}
	&_advantages {
		padding: 75px 0 20px;
	}
	&_choose {
		background-repeat: no-repeat;
		background-position: center center;
		background-size: cover;
	}
	&_contacts {
		background-color: #292929;
		padding: 75px 0;
		.section__title {
			color: white;
		}
	}
	&_timeline {
		padding: 70px 0 135px;
	}
	&_partner {
		background-repeat: no-repeat;
		background-position: center center;
		background-size: cover;
		padding: 65px 0 58px;
	}
	&_red {
		padding: 0;
		background-color: #ff2524;
	}


	&__title {
		color: #000000;
		.medium();
		text-align: center;
		.font-size(40);
		line-height: 1.35;
		position: relative;
		padding-bottom: @icon-squares-height + 5px;
		margin-bottom: 50px;
		&:after {
			content: "";
			display: block;
			.sprite(@icon-squares);
			position: absolute;
			bottom: 0;
			left: 50%;
			margin-left: -@icon-squares-width / 2;
		}
	}
}