.boosters-list {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	align-items: center;
	justify-content: flex-start;
	&__item {
		margin-right: 45px;
		&:last-child {
			margin-right: 0;
		}
	}
	&__link {
		text-decoration: none;
		display: block;
	}
	&__img-wrap {
		width: 120px;
		height: 120px;
		display: flex;
		justify-content: center;
		align-items: center;
		margin-bottom: 22px;
	}
	&__image {
		vertical-align: middle;
		max-width: 100%;
		max-height: 100%;
	}
	&__name {
		text-align: center;
		color: #ffffff;
		.regular();
		.font-size(18);
		line-height: 1.33;
	}
}