.btn {
	display: inline-block;
	text-decoration: none;
	border-radius: 25px;
	cursor: pointer;
	background-color: #fff;
	border: 1px solid #ddd;
	padding: 14px 25px 13px;
	text-align: center;
	transition: background-color .2s ease, border-color .2s ease;
	&__icon-wrap {
		width: 22px;
		height: 22px;
		margin: 0 4px;
		position: relative;

		.icon {
			opacity: 1;
			position: absolute;
			top: 0;
			left: 0;
			transition: opacity .2s ease;
			&:last-child {
				opacity: 0;
			}
		}
	}
	&__text {
		text-transform: capitalize;
		.medium();
		color: #999;
		.font-size(14);
		transition: color .2s ease;
	}
	&:hover {
		background-color: #da251e;
		border-color: #da251e;
		.btn__text {
			color: white;
		}
	}
	&:active, &:focus {
		background-color: #c61c03;
		border-color: #c61c03;
		.btn__text {
			color: white;
		}
	}

	&_circle {
		border-radius: 50%;
		width: 46px;
		height: 46px;
		padding: 0;
		opacity: 0;
		transition: all .2s ease;
	}
	&_compare {
		background: #ffffff url(../img/icon-compare.png) no-repeat scroll center -43px;
		transform: translate(20px, 0);
		&:hover {
			background-position: center 6px;
		}
	}
	&_wishlist {
		background: #ffffff url(../img/icon-wishlist.png) no-repeat scroll center -33px;
		transform: translate(-20px, 0px);
		&:hover {
			background-position: center 6px;
		}
	}
	&_icon {
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
		border-color: #da251e;
		line-height: normal;
		height: auto;
		padding: 10px 25px;
		&:hover, &:focus {
			.icon {
				opacity: 0;
				&:last-child {
					opacity: 1;
				}
			}
		}
		.btn__text {
			padding-top: 5px;
			margin: 0 4px;
			color: #292929;
		}
	}
	&_transparent {
		background-color: transparent;
		border-color: #da251d;
		border-width: 2px;
		padding: 18px 25px 16px;
		border-radius: 32px;
		.btn__text {
			.font-size(18);
			color: #000000;
		}
	}

	.icon {
		display: block;
		&.icon-home {
			.sprite(@icon-home);
		}
		&.icon-home-white {
			.sprite(@icon-home-white);
		}
		&.icon-building {
			.sprite(@icon-building);
		}
		&.icon-building-white {
			.sprite(@icon-building-white);
		}
		&.icon-call {
			.sprite(@icon-call);
		}
		&.icon-call-white {
			.sprite(@icon-call-white);
		}
		&.icon-3g {
			.sprite(@icon-3g);
		}
		&.icon-3g-white {
			.sprite(@icon-3g-white);
		}
		&.icon-4g {
			.sprite(@icon-4g);
		}
		&.icon-4g-white {
			.sprite(@icon-4g-white);
		}
	}
}