.advantages {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: flex-start;
	align-items: flex-start;
	&__item {
		width: calc(~"(100% - 85px * 2) / 3");
		margin: 0 85px 55px 0;
		&:nth-child(3n) {
			margin-right: 0;
		}
	}
	&__icon-wrap {
		width: 130px;
		height: 130px;
		border-radius: 50%;
		background-color: #ff262b;
		display: flex;
		justify-content: center;
		align-items: center;
		margin: 0 auto 28px;
	}
	&__about {
		text-align: center;
	}
	&__title {
		color: #000000;
		.font-size(24);
		.medium();
		line-height: 1;
		margin-bottom: 22px;
	}
	&__descr {
		color: #000000;
		.font-size(16);
		.regular();
		line-height: 1.5;
	}

	.icon {
		display: block;
		&.icon-money {
			.sprite(@icon-money);
		}
		&.icon-certificate-big {
			.sprite(@icon-certificate-big);
		}
		&.icon-signal {
			.sprite(@icon-signal);
		}
		&.icon-cargo {
			.sprite(@icon-cargo);
		}
		&.icon-list {
			.sprite(@icon-list);
		}
		&.icon-filter {
			.sprite(@icon-filter);
		}
	}
}