.components {
	&__item {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
		margin-bottom: 5px;
		color: #000000;
		.regular();
		.font-size(16);
		line-height: 1.13;
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__icon-wrap {
		width: 32px;
		height: 36px;
		display: flex;
		justify-content: center;
		align-items: center;
		margin-right: 15px;
		flex-shrink: 0;
	}
	&__icon {
		vertical-align: middle;
		max-width: 100%;
		max-height: 100%;
	}
}