.boosters {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;
	&__col {
		padding: 56px 0;
		&_left {
			width: 45%;
			background: url("../img/arrows.jpg") no-repeat center right;
			background-size: auto;
		}
		&_right {
			padding-left: 20px;
			width: 55%;
		}
	}
	&__title {
		max-width: 240px;
		color: #ffffff;
		.regular();
		.font-size(40);
		line-height: 1.43;
		text-transform: uppercase;
	}
}