.watch-video {
	max-width: 1100px;
	margin: 0 auto;
	display: flex;
	justify-content: flex-start;
	align-items: flex-start;
	flex-direction: row;
	&__col {
		&_video {}
		&_about {
			padding-left: 85px;
		}
	}
	&__descr {
		color: #000000;
		.regular();
		.font-size(16);
		line-height: 1.5;
		margin-bottom: 25px;
	}
}