.product {
	background: white;
	border: 1px solid #ccc;
	border-radius: 10px;
	box-shadow: transparent 0 0 0 2px;
	padding: 8px;
	transition: border .2s ease, box-shadow .2s ease;
	overflow: hidden;
	&:hover {
		border-color: transparent;
		box-shadow: rgb(218, 37, 29) 0 0 0 2px;
		.product__quick-view {
			transform: translateY(0%);
			opacity: 1;
		}
		.product__btn_main {
			background-color: #da251e;
			border-color: #da251e;
			.btn__text {
				color: white;
			}
			&:hover {
				background-color: #c61c03;
				border-color: #c61c03;
				.btn__text {
					color: white;
				}
			}
		}
		.btn_compare {
			transform: translate(0, 0);
			opacity: 1;
		}
		.btn_wishlist {
			transform: translate(0, 0);
			opacity: 1;
		}
	}
	&__image-wrap {
		position: relative;
	}
	&__quick-view {
		display: block;
		position: absolute;
		top: 50%;
		left: 50%;
		margin-top: -30px;
		margin-left: -30px;
		z-index: 5;
		text-decoration: none;
		border-radius: 50%;
		width: 60px;
		height: 60px;
		background: #da251e;
		transform: translateY(50%);
		opacity: 0;
		transition: transform .2s ease, opacity .2s ease;
		&:after {
			content: "";
			.sprite(@icon-search);
			position: absolute;
			top: 50%;
			left: 50%;
			margin-top: -@icon-search-height / 2;
			margin-left: -@icon-search-width / 2;
		}
		&:hover {
			background: #c61c03;
		}
	}
	&__link {
		text-decoration: none;
	}
	&__image {
		vertical-align: middle;
		max-width: 100%;
	}
	&__body {
		text-align: center;
	}
	&__name {
		text-transform: capitalize;
		text-overflow: ellipsis;
		color: #444444;
		margin-bottom: 8px;
		padding: 0 5px;
		.medium();
		.font-size(14);
	}
	&__name-link {
		text-decoration: none;
		color: #444444;
		display: inline-block;
		vertical-align: middle;
		white-space: nowrap;
		transition: color .2s ease;
		&:hover {
			color: #da251e;
		}
	}
	&__descr {
		.poppins-regular();
		.font-size(13);
		line-height: 1.66;
		color: #666666;
		margin-bottom: 3px;
	}
	&__price-box {}
	&__actions {

		.btn {
			vertical-align: middle;
		}
	}
	&__btn {
		&_main {}
		&_secondary {}
	}
	&__footer {
		padding-top: 5px;
		padding-bottom: 16px;
		text-align: center;
	}
	&__details {
		text-decoration: none;
		.font-size(13);
		color: #444;
		.poppins-regular();
		transition: color .2s ease;
		&:hover {
			color: #da251e;
		}
	}
}