.become-partner {
	max-width: 705px;
	margin: 0 auto;
	&__title {
		color: #000000;
		.regular();
		.font-size(16);
		line-height: 1.5;
		text-align: center;
		margin-bottom: 33px;
	}
	&__btn-wrap {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: flex-start;
		align-items: center;

		.btn {
			display: flex;
			justify-content: center;
			align-items: center;
			width: calc(~"(100% - (14px * 4)) / 2");
			margin: 12px 14px;
			padding: 9px 25px;
			min-height: 64px;
		}
	}
}