.contacts {
	text-align: center;
	color: #ffffff;
	.font-size(16);
	.regular();
	line-height: 1.5;
	&__row {
		margin-bottom: 10px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__text {
		margin-right: 5px;
	}
	&__value {
		text-decoration: none;
		color: #ffffff;
		&:hover {
			text-decoration: underline;
		}
	}
}