.intro-advantages {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: flex-start;
	align-items: flex-start;
	&__col {
		margin-right: 10px;
	}
	&__item {
		border-radius: 25px;
		border: 1px solid transparent;
		padding: 6px 18px;
		transition: border .2s ease;
		margin-bottom: 5px;
		&:last-child {
			margin-bottom: 0;
		}
		&_active {
			border-color: #da251d;
		}
		&:hover {
			border-color: #da251d;
		}
	}
	&__item-inner {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
	}
	&__text {
		color: #000000;
		.regular();
		.font-size(24);
		line-height: 1.2;
	}

	.icon {
		display: inline-block;
		margin-right: 12px;
		&.icon-time {
			.sprite(@icon-time);
		}
		&.icon-world {
			.sprite(@icon-world);
		}
		&.icon-certificate {
			.sprite(@icon-certificate);
		}
		&.icon-delivery {
			.sprite(@icon-delivery);
		}
		&.icon-ribbon {
			.sprite(@icon-ribbon);
		}
	}
}