.tabs {
	&__nav {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: center;
		align-items: center;
		margin-bottom: 50px;
	}
	&__nav-item {
		margin: 0 13px;
		min-width: 172px;
		&_current {
			.tabs__nav-link {
				background-color: #da251e;
				.icon {
					opacity: 0;
					&:last-child {
						opacity: 1;
					}
				}
				.btn__text {
					color: white;
				}
			}
		}
	}
	&__nav-link {}
	&__content-wrap {}
	&__content {
		display: none;
		visibility: hidden;
		&:first-child {
			display: block;
			visibility: visible;
		}
	}
}