.timeline {
	max-width: 780px;
	margin: 0 auto;
	&__item {
		position: relative;
		&:after {
			content: "";
			width: 2px;
			height: 100%;
			position: absolute;
			top: 0;
			left: 50%;
			margin-left: -1px;
			background-color: #da251e;
		}
		&:first-child {
			&:after {
				height: 50%;
				top: auto;
				bottom: 0;
			}
		}
		&:last-child {
			&:after {
				height: 50%;
			}
		}
		&:nth-child(even) {
			.timeline__image-wrap {
				order: 3;
			}
			.timeline__about {
				order: 1;
			}
		}
	}
	&__inner-wrap {
		position: relative;
		z-index: 1;
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
	}
	&__image-wrap {
		order: 1;
		width: calc(~"(100% - (60px + 54px)) / 2");
		max-height: 227px;
	}
	&__image {
		vertical-align: middle;
		max-width: 100%;
	}
	&__count {
		order: 2;
		display: flex;
		margin: 0 30px;
		flex-shrink: 0;
		justify-content: center;
		align-items: center;
		width: 54px;
		height: 54px;
		border-radius: 50%;
		border: 1px solid #da251d;
		background-color: #ffffff;
		color: #da251d;
		.font-size(24);
		.medium();
		line-height: 1;
		padding-top: 3px;
	}
	&__about {
		order: 3;
		width: calc(~"(100% - (60px + 54px)) / 2");
		color: #2a2a28;
		.font-size(18);
		.regular();
		line-height: 1.33;
	}
}
